.i{
    display: flex;
    height: 100vh;
}

.i-left{
    flex: 1;
    display: flex; 
    align-items: center;
    justify-content: center;
}

.i-right{
    flex: 1;
    position: relative;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
    /* background-color: burlywood; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
    color: #777;
    /* font-weight: 500;     */
    /* color: rgb(254, 55, 55); */
}

.i-and {
    font-weight: 500;
    font-size: 25px;
    color:red;
}
.i-title {
    height: 50px;
    overflow:hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate
}

@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color:blueviolet;
    display:flex;
    align-items: center;
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.i-bg {
    clip-path: polygon(100% 0%, 100% 54%, 100% 100%, 50% 100%, 0% 50%, 44% 0);
    background-color:lemonchiffon;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;    
}

@media screen and (max-width: 480px) {
    .i{
        flex-direction: column;
        /* padding-top: 10%; */
    }
    .i-left-wrapper {
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }  
    .i-title-item {
        /* text-align: center; */
        justify-content: center;
        /* align-items: center; */
    }
    .i-desc {
        display: none;
    }
    
}